:root {
  /*  --primary-color: 39, 50, 114;
  --secondary-color: 255, 94, 20;
  --tertiary-color: 0, 0, 0;
  --light-grey: 245, 245, 245;
  --text-color: 102, 102, 102; */
  --poppins: "Poppins", sans-serif;
  --roboto: "Roboto", sans-serif;
}

* {
  margin: 0;
  padding: 0;
  border: none;
}

html {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  scroll-behavior: smooth;
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  max-width: 100vw;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: 2rem;
  line-height: 3.4rem;
  font-family: var(--roboto);
}
@media (min-width: 768px) {
  body {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

section {
  max-width: 100vw;
}

/*Headings*/
h1,
h2,
h3,
h4,
h5 {
  font-family: var(--poppins);
  color: rgba(var(--primary-color));
}

h1,
.h1,
h1 *,
.h1 * {
  font-size: 2.8rem;
  font-weight: 600;
  line-height: 5.8rem;
  letter-spacing: 0.5px;
  font-family: var(--poppins);
}
@media (min-width: 768px) {
  h1,
  .h1,
  h1 *,
  .h1 * {
    font-size: 4rem;
    line-height: 7rem;
  }
}
@media (min-width: 1441px) {
  h1,
  .h1,
  h1 *,
  .h1 * {
    font-size: 7.8rem;
    line-height: 10rem;
  }
}

h2,
.h2 {
  font-size: 3rem;
  font-weight: 600;
  font-family: var(--poppins);
  line-height: 4.5rem;
}
@media (min-width: 768px) {
  h2,
  .h2 {
    font-size: 4rem;
    line-height: 5rem;
  }
}
@media (min-width: 1441px) {
  h2,
  .h2 {
    font-size: 5rem;
    line-height: 6.5rem;
  }
}

h3,
.h3,
.h3 *,
h3 * {
  font-family: var(--poppins);
  font-size: 2.4rem;
  font-weight: 600;
  line-height: normal;
}

h4,
.h4,
.h4 *,
h4 * {
  font-family: var(--poppins);
  font-size: 2.4rem;
  font-weight: 600;
  letter-spacing: 0.5px;
  line-height: normal;
}
@media (min-width: 1441px) {
  h4,
  .h4,
  .h4 *,
  h4 * {
    font-size: 3.2rem;
    line-height: 4.6rem;
  }
}

h5,
.h5,
.h5 *,
h5 * {
  font-family: var(--poppins);
  font-size: 1.8rem;
  line-height: 3.2rem;
  font-weight: 600;
}
@media (min-width: 768px) {
  h5,
  .h5,
  .h5 *,
  h5 * {
    font-size: 2.5rem;
    line-height: 3.5rem;
  }
}

h6,
.h6,
.h6 *,
h6 * {
  font-family: var(--poppins);
  font-size: 2.3rem;
  line-height: 2.6rem;
  font-weight: 600;
}

/*Globales*/
a {
  font-family: var(--roboto);
  color: rgb(var(--primary-color));
  font-size: 2rem;
  line-height: 3.4rem;
  text-decoration: none;
  transition: color 0.2s ease-in-out;
}
@media (min-width: 768px) {
  a {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}

a:hover,
a:active,
a:focus {
  color: rgb(var(--secondary-color));
  /* text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.25); */
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li a {
  width: 100%;
  height: 100%;
  display: block;
}

p {
  font-family: var(--roboto);
  color: rgb(var(--text-color));
  font-size: 2rem;
  line-height: 3.4rem;
}
@media (min-width: 768px) {
  p {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}

picture {
  display: flex;
}

img {
  max-width: 100%;
  height: auto;
  transition: opacity 0.3s;
}

.txt-center {
  text-align: center;
}

.container {
  max-width: 1230px;
  width: 90%;
  margin: 0 auto;
}
.container.page {
  padding: 8rem 0;
}

.formatted-text {
  font-size: 2rem;
  font-family: var(--roboto);
  font-weight: 400;
  color: rgba(var(--text-color), 1);
  line-height: 3.4rem;
}
@media (min-width: 768px) {
  .formatted-text {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}
.formatted-text p,
.formatted-text span,
.formatted-text li {
  margin-bottom: 1.6rem;
  color: rgba(var(--text-color), 1);
  font-size: 2rem;
  font-family: var(--roboto);
  font-weight: 400;
  line-height: 3.4rem;
  vertical-align: text-top;
}
@media (min-width: 768px) {
  .formatted-text p,
  .formatted-text span,
  .formatted-text li {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}

.section-title {
  font-size: 2.6rem;
  font-family: var(--poppins);
  font-weight: bold;
  color: rgb(var(--primary-complement));
  letter-spacing: 2px;
  line-height: 3.2rem;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .section-title {
    font-size: 3rem;
    line-height: 3.4rem;
  }
}
@media (min-width: 1024px) {
  .section-title {
    font-size: 4rem;
    line-height: 4.4rem;
  }
}

[type=submit],
.btn {
  min-width: fit-content;
  padding: 7.5px 15px;
  display: inline-block;
  transition: 0.3s ease-in-out;
}
[type=submit] > *,
.btn > * {
  font-family: var(--poppins);
  font-size: 2rem;
  line-height: 3.4rem;
  text-align: center;
}
@media (min-width: 768px) {
  [type=submit] > *,
  .btn > * {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}
[type=submit].white,
.btn.white {
  background-color: #fff;
  color: rgb(var(--text-color));
}
[type=submit].white:hover,
.btn.white:hover {
  background-color: rgb(var(--primary-color), 1);
  color: #fff;
}
[type=submit].primary,
.btn.primary {
  background-color: rgb(var(--primary-color));
  color: #fff;
}
[type=submit].primary:hover,
.btn.primary:hover {
  background-color: #fff;
  color: #000;
}
[type=submit].secondary,
.btn.secondary {
  background-color: rgb(var(--secondary-color));
  color: #fff;
}
[type=submit].secondary:hover,
.btn.secondary:hover {
  background-color: rgb(var(--primary-color), 1);
  color: #fff;
}
[type=submit].arrow,
.btn.arrow {
  padding: 20px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
}
[type=submit].arrow::after,
.btn.arrow::after {
  content: "→";
  margin-left: 1rem;
  display: inline-block;
  font-size: 2.5rem;
}

.flx-center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.sidebar {
  width: 100%;
}
.sidebar .widget {
  width: 100%;
  margin: 4rem 0;
  padding: 3.5rem 4rem;
  background-color: rgba(var(--primary-complement));
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.sidebar .widget ul {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 2.5rem;
  list-style: disc;
  list-style-position: inside;
}
.sidebar .widget ul li {
  width: 100%;
  text-align: left;
  font-family: var(--poppins);
  font-size: 1.8rem;
  font-weight: 700;
  color: #fff;
}
.sidebar .widget ul li:hover a {
  color: rgba(var(--tertiary-color));
}
.sidebar .widget ul li a {
  display: inline;
  font-family: var(--poppins);
  color: #fff;
  transition: 0.2s ease-in-out;
}
.sidebar .widget__title {
  margin-bottom: 2rem;
  font-weight: 700;
  color: #fff;
}
.sidebar .widget__content {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.post-pagination {
  width: 100%;
  flex: 0 0 100%;
  grid-column: 1/4;
  display: flex;
  gap: 15px;
  justify-content: center;
  align-items: center;
}
.post-pagination a,
.post-pagination span {
  padding: 1rem;
  font-size: 2rem;
  line-height: 2.4rem;
  font-weight: bold;
}
@media (min-width: 768px) {
  .post-pagination a,
  .post-pagination span {
    font-size: 1.8rem;
    line-height: 3.2rem;
  }
}

/* Properly align images embeded in content */
.wp-block-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

img {
  max-width: 100%;
  max-height: 100%;
}

a:has(img.alignleft) {
  float: left;
  display: flex;
}

.wp-block-image .alignleft {
  float: left;
  display: block;
  margin: 10px 20px 0px 0px;
  max-width: 100%;
}
.wp-block-image .alignleft img {
  display: block;
}
@media (max-width: 500px) {
  .wp-block-image .alignleft {
    float: none;
    width: 100%;
    height: auto;
    margin: 0px 0px 20px 0px !important;
  }
  .wp-block-image .alignleft img {
    width: 100%;
    height: auto;
  }
}

p:has(.alignleft) {
  max-width: 100%;
  width: fit-content;
  margin: 0;
  margin-right: 20px;
  display: flex;
  float: left;
}
@media (max-width: 500px) {
  p:has(.alignleft) {
    width: 100%;
  }
}
p:has(.alignleft) img.alignleft {
  margin: 0;
  margin-right: 0;
}

.alignleft {
  margin-right: 20px;
  margin-bottom: 35px;
  float: left;
}
@media (max-width: 500px) {
  .alignleft {
    width: 100%;
  }
}
.alignleft img {
  width: 100% !important;
  height: auto !important;
}

.aligncenter {
  max-width: 100%;
}
.aligncenter img {
  width: 100%;
  height: auto;
  display: block;
}

img.aligncenter {
  display: block;
  max-width: 100%;
  height: auto;
}

iframe {
  max-width: 100%;
  width: 100%;
}

@media (max-width: 767px) {
  .hide-on-mobile {
    display: none !important;
  }
}
@media (max-width: 1023px) {
  .hide-on-tablet {
    display: none !important;
  }
}
@media (min-width: 1024px) {
  .hide-on-desktop {
    display: none !important;
  }
}
.site-header {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.site-header .container {
  max-width: 1260px;
  margin: 0 auto;
}
.site-header #logo {
  max-width: 160px;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.site-header #logo img {
  object-fit: contain;
}
.site-header__top {
  width: 100%;
  padding: 20px 10px;
  background-color: rgba(var(--primary-color), 1);
}
@media (min-width: 1441px) {
  .site-header__top {
    padding: 8px 50px;
  }
}
.site-header__top .container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .site-header__top .container {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 15px;
  }
}
@media (min-width: 1441px) {
  .site-header__top .container {
    gap: 35px;
  }
}
.site-header__top .container a {
  display: flex;
  transition: 0.3s linear;
}
.site-header__top .container a:hover {
  color: #fff;
}
.site-header__top .container a:hover span {
  color: #fff;
}
.site-header__top .container a:hover svg {
  fill: #fff;
}
.site-header__top .container span {
  font-family: var(--roboto);
  font-size: 13px;
  line-height: 25px;
  font-weight: 400;
  font-style: normal;
  color: #bcbcbc;
  transition: 0.3s linear;
}
@media (min-width: 768px) {
  .site-header__top .container span {
    font-size: 16px;
    line-height: 28px;
  }
}
.site-header__top .container svg {
  width: 16px;
  height: 16px;
  fill: #bcbcbc;
  transition: 0.3s linear;
}
.site-header__top .container .label {
  margin-bottom: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
}
@media (min-width: 1441px) {
  .site-header__top .container .label {
    margin-bottom: 0;
  }
}
.site-header__top .container .contact-links {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  gap: 15px;
}
@media (min-width: 768px) {
  .site-header__top .container .contact-links {
    flex-direction: row;
    justify-content: center;
    gap: 0;
    flex: 0 0 100%;
  }
  .site-header__top .container .contact-links li:first-child {
    padding-right: 10px;
    position: relative;
  }
  .site-header__top .container .contact-links li:first-child::after {
    content: "|";
    position: absolute;
    left: 98%;
    top: 50%;
    transform: translateY(-50%);
    display: block;
    font-size: 22px;
    font-weight: 300;
    color: #bcbcbc;
  }
  .site-header__top .container .contact-links li:last-child {
    padding-left: 10px;
  }
  .site-header__top .container .contact-links li .label {
    margin-bottom: 0;
  }
}
@media (min-width: 1441px) {
  .site-header__top .container .contact-links {
    margin-bottom: 0;
    flex: 1;
    justify-content: flex-start;
  }
}
.site-header__top .container .social-networks {
  margin-bottom: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 22px;
}
@media (min-width: 1441px) {
  .site-header__top .container .social-networks {
    margin-bottom: 0;
  }
}
.site-header__bottom {
  width: 100%;
  padding: 40px 20px;
  background-color: #fff;
}
@media (min-width: 1441px) {
  .site-header__bottom {
    padding: 0 50px;
  }
}
.site-header__bottom .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}
@media (min-width: 1024px) {
  .site-header__bottom .container {
    justify-content: space-between;
  }
}
.site-header__bottom #main-nav-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}
.site-header__bottom #menu-mobile {
  padding: 8px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
}
@media (min-width: 1024px) {
  .site-header__bottom #menu-mobile {
    display: none;
  }
}
.site-header__bottom #menu-mobile span {
  margin-top: 3px;
  font-family: var(--poppins);
  font-size: 16px;
  line-height: 16px;
  color: #616263;
}
.site-header__bottom #menu-mobile svg {
  height: 100%;
  width: 16px;
  display: block;
}
.site-header__bottom #menu-mobile svg path {
  fill: #616263;
}
.site-header__bottom #nav-wrapper {
