}
@media (min-width: 1441px) {
  .cover-slider__inner .slide__inner .container.right .content {
    max-width: 600px;
  }
}
.cover-slider__inner .slide__inner .container.left {
  justify-content: flex-start;
}
@media (min-width: 1441px) {
  .cover-slider__inner .slide__inner .container.left .content {
    max-width: 600px;
  }
}
.cover-slider__inner .slide__inner .content {
  max-width: 305px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 1024px) {
  .cover-slider__inner .slide__inner .content {
    max-width: 100%;
  }
}
.cover-slider__inner .slide__inner .content * {
  text-align: left;
}
.cover-slider__inner .slide__inner .content.visible .title {
  opacity: 1;
  transform: translateY(0);
  color: #fff;
}
.cover-slider__inner .slide__inner .content.visible .title::before {
  width: calc(100% + 12px);
}
.cover-slider__inner .slide__inner .content.visible .title::after {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  animation-name: height-effect;
  animation-timing-function: ease-in;
  animation-duration: 0.8s;
  animation-delay: 1s;
}
@media (min-width: 768px) {
  .cover-slider__inner .slide__inner .content.visible .title::after {
    animation-name: height-effect-tablet;
  }
}
@media (min-width: 1024px) {
  .cover-slider__inner .slide__inner .content.visible .title::after {
    animation-name: height-effect-ldpi;
  }
}
@keyframes height-effect {
  0% {
    height: 0;
    bottom: 0;
  }
  50% {
    height: calc(100% - 5.8rem);
  }
  75% {
    height: calc(100% - 5.8rem);
    top: 5.8rem;
    bottom: auto;
  }
  100% {
    height: 0;
    top: 5.8rem;
    bottom: auto;
  }
}
@keyframes height-effect-tablet {
  0% {
    height: 0;
    bottom: 0;
  }
  50% {
    height: calc(100% - 7rem);
  }
  75% {
    height: calc(100% - 7rem);
    top: 7rem;
    bottom: auto;
  }
  100% {
    height: 0;
    top: 7rem;
    bottom: auto;
  }
}
@keyframes height-effect-ldpi {
  0% {
    height: 0;
    bottom: 0;
  }
  50% {
    height: calc(100% - 9rem);
  }
  75% {
    height: calc(100% - 9rem);
    top: 9rem;
    bottom: auto;
  }
  100% {
    height: 0;
    top: 9rem;
    bottom: auto;
  }
}
.cover-slider__inner .slide__inner .content.visible .subtitle {
  opacity: 1;
  transform: scale(1);
}
.cover-slider__inner .slide__inner .content.visible .btn {
  width: fit-content;
  padding: 7.5px 15px;
}
@media (min-width: 1024px) {
  .cover-slider__inner .slide__inner .content.visible .btn {
    padding: 10px 30px;
  }
}
.cover-slider__inner .slide__inner .content .title {
  position: relative;
  font-size: 2.1rem;
  font-weight: 400;
  color: transparent;
  transform: translateY(300px);
  transition: color 1.8s ease 1.2s, transform 0.6s ease;
}
@media (min-width: 1024px) {
  .cover-slider__inner .slide__inner .content .title {
    font-size: 6rem;
    line-height: 9rem;
  }
}
.cover-slider__inner .slide__inner .content .title::first-line {
  font-weight: 600;
}
.cover-slider__inner .slide__inner .content .title::before {
  content: "";
  height: 5.8rem;
  width: 0;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
  background-color: rgba(var(--secondary-color), 1);
  transition: 0.4s ease-in-out 0.4s;
  transition-delay: 0.4s;
}
@media (min-width: 1024px) {
  .cover-slider__inner .slide__inner .content .title::before {
    height: 9rem;
  }
}
.cover-slider__inner .slide__inner .content .title::after {
  content: "";
  width: calc(100% + 12px);
  margin-top: 5px;
  z-index: 3;
  background-color: #fff;
}
.cover-slider__inner .slide__inner .content .subtitle {
  margin-bottom: 50px;
  color: #fff;
  font-size: 2rem;
  opacity: 0;
  transform: scale(0);
  transition: opacity 1.5s ease 0.8s, transform 1.2s ease 0.6s;
}
.cover-slider__inner .slide__inner .content .btn {
  width: 0;
  padding: 7.5px 0;
  position: relative;
  left: 0;
  overflow: hidden;
  display: flex;
  transition: 0.3s linear 1.8s, background-color 0.3s linear;
}
@media (min-width: 1024px) {
  .cover-slider__inner .slide__inner .content .btn {
    padding: 10px 0;
  }
}
.cover-slider__inner .slide__inner .content .btn span {
  text-wrap: nowrap;
}
.cover-slider__inner .slide__inner .content .btn:hover {
  background-color: rgb(var(--primary-color));
}
.cover-slider__inner .slide__inner .content .btn:hover span {
  color: #fff;
  transition: 0.3s linear;
}

